import styles from './Pnf.module.css'; // Corrected import
import { Link } from 'react-router-dom';

function Pnf() {
  return (
    <div className={styles.pnfContainer}> {/* Apply class from modular CSS */}
      <div className={styles.pnfMessage}> {/* Apply class from modular CSS */}
        <h1>Error 404</h1>
        <p>Page Not Found</p>
        <Link to={'/'}>
          <button className={styles.pnfBtn}> {/* Apply class from modular CSS */}
            Go Back to HOME
          </button>
        </Link>
      </div>
    </div>
  );
}

export default Pnf;
