import Footer from "../features/common/Footer";
import Navbar from "../features/common/Navbar";
import styles from "./AboutPage.module.css";

function AboutPage() {
  return (
    <div>
      <Navbar />
      <div className={styles.homeContainer}>
        <div className={styles.aboutContainer}>
          <h1>About TaskList</h1>
          <p>
            TaskList is your ultimate to-do list manager designed to help you
            stay organized and productive.
          </p>
          <p>
            Easily create, manage, and track your daily tasks with our intuitive
            and user-friendly interface. Whether you're managing work projects
            or personal errands, TaskList keeps you on top of your goals.
          </p>
          <p>Start organizing your life today with TaskList!</p>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default AboutPage;
