import axios from "axios";
import { store } from "../../app/store";
import { setAccessToken } from "../auth/authSlice";

const api = axios.create({
  baseURL: "/",
  withCredentials: true,
});

api.interceptors.request.use(
  (config) => {
    const state = store.getState();
    const token = state.auth.accessToken;
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

api.interceptors.response.use(
  (response) => response,
  async (error) => {
    if (error.response?.status === 401) {
      try {
        const { data } = await axios.post(
          "/auth/refresh",
          {},
          { withCredentials: true }
        );
        const newToken = data.access_token;

        store.dispatch(setAccessToken(newToken));
        localStorage.setItem("accessToken", newToken);

        error.config.headers.Authorization = `Bearer ${newToken}`;
        return api(error.config);
      } catch (refreshError) {
        console.error("Token refresh failed:", refreshError);
        localStorage.removeItem("accessToken");
        document.cookie =
          "refresh_token=; expires=Thu, 01 Jan 1970 00:00:00 GMT; path=/";
        return Promise.reject(refreshError);
      }
    }
    return Promise.reject(error);
  }
);

export function createTask(obj) {
  return api.post(`/tasks/create`, obj);
}

export function fetchAllTasks() {
  return api.get(`/tasks`);
}

export function deleteTask(id) {
  return api.delete(`/tasks/delete/${id}`);
}

export function updateTask(obj) {
  return api.patch(`/tasks/update/${obj.id}`, obj);
}
