import axios from "axios";
import { store } from "../../app/store"; // Import default store
import { setAccessToken } from "./authSlice";

const api = axios.create({
  baseURL: "/", // Replace with your actual backend URL
  withCredentials: true,
});

api.interceptors.request.use(
  (config) => {
    const state = store.getState(); // Access Redux state
    const token = state.auth.accessToken; // Replace with the actual path to your token
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// Response interceptor to handle token refresh
api.interceptors.response.use(
  (response) => response,
  async (error) => {
    const originalRequest = error.config;
    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true; // Prevent infinite loops
      try {
        const { data } = await axios.post(
          "/auth/refresh",
          {},
          { withCredentials: true } // Include credentials to send the refresh token cookie
        );
        const newToken = data.access_token;

        // Save the new access token to Redux and localStorage
        store.dispatch(setAccessToken(newToken));
        localStorage.setItem("accessToken", newToken);

        // Update the authorization header with the new token
        originalRequest.headers.Authorization = `Bearer ${newToken}`;
        originalRequest.withCredentials = true; // Ensure cookies are sent

        // Retry the original request with the new token
        return api(originalRequest);
      } catch (refreshError) {
        // Handle token refresh failure

        // Remove access token from localStorage
        localStorage.removeItem("accessToken");

        // Optionally, remove the refresh token if it's stored in cookies
        document.cookie =
          "refresh_token=; expires=Thu, 01 Jan 1970 00:00:00 GMT; path=/";

        // Optionally, redirect to login page or dispatch a logout action
        // window.location.href = '/login';
        // store.dispatch(logout());

        return Promise.reject(refreshError);
      }
    }
    return Promise.reject(error);
  }
);


export const fetchUserInfo = () => api.get("/auth/userinfo");
export const logOut = () => api.post("/auth/logout");
export const signUpUser = (userInfo) => api.post("/auth/signup", userInfo);
export const loginUser = (userInfo) => api.post("/auth/login", userInfo);
export const forgotUser = (userInfo) =>
  api.post("/auth/forgot-password", userInfo);
export const resetUser = (password, id, reset_token) =>
  api.post(`/auth/reset-password/${id}/${reset_token}`, { password });
export const changePass = (password) =>
  api.post("/auth/change-password", { password });
export const verifyOtp = ({ email, otp }) =>
  api.post("/auth/verify-email", { email, otp });
