import { useSelector, useDispatch } from "react-redux";
import { fetchTaskAsync, deleteTaskAsync, clear } from "./taskSlice";
import { useEffect, useState } from "react";
import Task from "./components/Task";
import styles from "./TaskList.module.css";
import TaskForm from "./TaskForm";
import Loader from "../common/Loader";
import Alert from "../common/Alert";

export function TaskList() {
  const { tasks, status, type, message } = useSelector((state) => state.task);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(clear()); // Clear message on mount
    dispatch(fetchTaskAsync());
    return () => {
      dispatch(clear()); // Clear message on unmount (navigating away)
    };
  }, [dispatch]);

  const [showForm, setShowForm] = useState(false);
  const [update, setUpdate] = useState(null);

  function handleClick() {
    dispatch(clear());
    setUpdate(null);
    setShowForm(!showForm);
  }

  function handleDelete(id) {
    dispatch(clear());
    dispatch(deleteTaskAsync(id));
  }

  function handleUpdate(id) {
    dispatch(clear());
    setUpdate(tasks.find((task) => task.id === id));
    setShowForm(true);
  }

  return (
    <div className={styles.container}>
      {status === "loading" && <Loader />} {/* Using the Loader component */}
      <section className={styles.newTask} onClick={handleClick}>
        <span>+</span>
      </section>
      {showForm ? (
        <TaskForm
          showForm={showForm}
          setShowForm={setShowForm}
          update={update}
          setUpdate={setUpdate}
        />
      ) : (
        <section className={styles.taskContainer}>
          {tasks?.length > 0 && (
            <p className={styles.taskListHeading}>All Tasks At A Glance</p> // Conditionally rendered
          )}
          {message && <Alert message={message} type={type} />}{" "}
          {/* Using the Alert component */}
          {tasks?.length > 0 ? (
            tasks.map((task) => (
              <div key={task.id} className={styles.taskCard}>
                <Task
                  {...task}
                  handleDelete={handleDelete}
                  handleUpdate={handleUpdate}
                />
              </div>
            ))
          ) : (
            <p className={styles.noTask}>
              Create your first task with TaskList!
            </p> // Message when no tasks exist
          )}
        </section>
      )}
    </div>
  );
}
