import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { changeAsync } from "../features/auth/authSlice";
import styles from "./ChangePage.module.css"; // Import the module CSS

function ChangePassword() {
  const [formData, setFormData] = useState({
    password: "",
  });

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const message = useSelector((state) => state.auth.message);
  const accessToken = useSelector((state) => state.auth.accessToken);

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const result = await dispatch(
      changeAsync({ password: formData.password, accessToken })
    );
    if (result.type === "auth/change/fulfilled") {
      setTimeout(() => {
        navigate("/");
      }, 3000);
    }
  };

  return (
    <div className={styles["login-container"]}>
      <form className={styles["login-form"]} onSubmit={handleSubmit}>
        <h2>Change Your Password?</h2>
        {message}
        <div className={styles["input-group"]}>
          <label htmlFor="password">Enter New Password</label>
          <input
            type="password"
            id="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
          />
        </div>
        <button type="submit" className={styles["login-btn"]}>
        Change Password
        </button>
        <p className={styles["redirect-text"]}>
          Cancel?{" "}
          <Link to="/" className={styles["signup-link"]}>
            Go to Home
          </Link>
        </p>
      </form>
    </div>
  );
}

export default ChangePassword;
