import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { verifyOtpAsync } from "../authSlice";
import { useNavigate } from "react-router-dom";
import styles from "./VerifyEmail.module.css";
import { useParams } from "react-router-dom";

const initialState = {
  otp: ["", "", "", ""], // Array to hold each digit of OTP
};

function VerifyEmail() {
  const [formData, setFormData] = useState(initialState);
  const message = useSelector((state) => state.auth.message);
  const { email } = useParams()
  console.log("Email From URL: ", email)
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleChange = (e, index) => {
    const value = e.target.value;
    if (/[^0-9]/.test(value)) return; // Allow only digits
    const newOtp = [...formData.otp];
    newOtp[index] = value;
    setFormData({
      ...formData,
      otp: newOtp,
    });

    // Move to next input box if the current one is filled
    if (value && index < 3) {
      document.getElementById(`otp-${index + 1}`).focus();
    }
  };

  const handleBackspace = (e, index) => {
    const value = e.target.value;
    if (value === "") {
      const newOtp = [...formData.otp];
      newOtp[index] = "";
      setFormData({
        ...formData,
        otp: newOtp,
      });
      if (index > 0) {
        document.getElementById(`otp-${index - 1}`).focus();
      }
    }
  };

  const handleVerifyOtp = async (e) => {
    e.preventDefault();
    const otp = formData.otp.join(""); // Join the OTP array into a string
    const result = await dispatch(verifyOtpAsync({email, otp}));
    if (result.type === "auth/verify/fulfilled") {
      navigate("/login"); // redirect to dashboard or next step
    }
  };

  return (
    <div className={styles.verifyContainer}>
      <form className={styles.verifyForm} onSubmit={handleVerifyOtp}>
        <h2>Enter OTP</h2>
        {message && <p className={styles.message}>{message}</p>}
        <p className={styles.email}>{email}</p>
        <div className={styles.inputGroup}>
          {formData.otp.map((digit, index) => (
            <input
              key={index}
              type="text"
              id={`otp-${index}`}
              name={`otp-${index}`}
              value={digit}
              onChange={(e) => handleChange(e, index)}
              onKeyDown={(e) => e.key === "Backspace" && handleBackspace(e, index)}
              maxLength={1}
              required
            />
          ))}
        </div>
        <button type="submit" className={styles.verifyBtn}>
          Verify OTP
        </button>
      </form>
    </div>
  );
}

export default VerifyEmail;
