import { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchUserInfoAsync } from "../features/auth/authSlice";
import styles from "./UserInfoPage.module.css";
import { Link } from "react-router-dom";
import Navbar from "../features/common/Navbar";
import Footer from "../features/common/Footer";

function UserInfoPage() {
  const accessToken = useSelector((state) => state.auth.accessToken);
  const userInfo = useSelector((state) => state.auth.user);
  const hasFetched = useRef(false);

  const dispatch = useDispatch();

  useEffect(() => {
    if (accessToken && !hasFetched.current) {
      hasFetched.current = true;
      dispatch(fetchUserInfoAsync(accessToken));
    }
  }, [dispatch, accessToken]);

  return (
    <>
    <Navbar/>
    <div className={styles["user-info-container"]}>
      <h1 className={styles["user-info-title"]}>User Info</h1>
      <p className={styles["user-info-detail"]}>
        <span>ID:</span> {userInfo?.id}
      </p>
      <p className={styles["user-info-detail"]}>
        <span>Name:</span> {userInfo?.name}
      </p>
      <p className={styles["user-info-detail"]}>
        <span>Email:</span> {userInfo?.email}
      </p>
      <p className={styles["user-info-detail"]}>
        <span>Account Created At:</span> {userInfo?.createdAt}
      </p>
      <p className={styles["redirect-text"]}>
        Want to change your password?{" "}
        <Link to="/userinfo/change-password" className={styles["signup-link"]}>
          Change Here
        </Link>
      </p>
    </div>
    <Footer/>
    </>
  );
}

export default UserInfoPage;
