import "./App.css";
import AboutPage from "./pages/AboutPage";
import HomePage from "./pages/HomePage";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import Pnf from "./pages/Pnf";
import SignUpPage from "./pages/SignUpPage";
import LoginPage from "./pages/LoginPage";
import UserInfoPage from "./pages/UserInfoPage";
import { useDispatch } from "react-redux";
import { useEffect } from "react";
import { setAccessToken } from "./features/auth/authSlice";
import ForgotPage from "./pages/ForgotPage";
import ResetPage from "./pages/ResetPage";
import ChangePassword from "./pages/ChangePage";
import Protected from "./features/auth/components/Protected";
import VerifyEmail from "./features/auth/components/VerifyEmail";
import RedirectPage from "./pages/RedirectPage";

const router = createBrowserRouter([
  {
    path: "/",
    element: (
      <Protected>
        <HomePage />
      </Protected>
    ),
  },
  {
    path: "/about",
    element: (
      <Protected>
        <AboutPage />
      </Protected>
    ),
  },
  {
    path: "/signup",
    element: <SignUpPage />,
  },
  {
    path: "/verify-email/:email",
    element: <VerifyEmail></VerifyEmail>,
  },
  {
    path: "/login",
    element: <LoginPage />,
  },
  {
    path: "/userinfo",
    element: (
      <Protected>
        <UserInfoPage />
      </Protected>
    ),
  },
  {
    path: "/userinfo/change-password",
    element: (
      <Protected>
        <ChangePassword />
      </Protected>
    ),
  },
  {
    path: "/forgot",
    element: <ForgotPage></ForgotPage>,
  },
  {
    path: "/reset-password/:id/:reset_token",
    element: <ResetPage></ResetPage>,
  },
  {
    path: "/redirect/:access_token",
    element: <RedirectPage></RedirectPage>,
  },
  {
    path: "*",
    element: <Pnf></Pnf>,
  },
]);

function App() {
  const dispatch = useDispatch();

  useEffect(() => {
    const accessToken = localStorage.getItem("accessToken");

    if (accessToken) {
      dispatch(setAccessToken(accessToken)); // Set the token in your Redux state
    }
  }, [dispatch]);

  return (
    <div className="App">
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
