import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { clear, updateTaskAsync } from "../taskSlice";
import Alert from "../../common/Alert";
import styles from "./UpdateTask.module.css";

function UpdateTaskForm({ setShowForm, update, setUpdate }) {
  const dispatch = useDispatch();
  const [add, setAdd] = useState(update);
  const { type, message } = useSelector((state) => state.task);

  useEffect(() => {
    setAdd(update);
  }, [update]);

  function handleChange(e) {
    setAdd({
      ...add,
      [e.target.name]:
        e.target.name === "completed"
          ? e.target.value === "true"
          : e.target.value,
    });
  }

  function handleSubmit() {
    dispatch(clear());
    dispatch(updateTaskAsync(add));
    setAdd({
      title: "",
      description: "",
      completed: false,
    });
    setUpdate(null);
    setShowForm(false);
  }

  return (
    <div className={styles.addTask}>
      {message && <Alert message={message} type={type} />}
      <h2>Update Task</h2>
      <input
        name="title"
        value={add.title}
        onChange={handleChange}
        type="text"
        placeholder="Enter New Task Title"
      />
      <input
        name="description"
        value={add.description}
        onChange={handleChange}
        type="text"
        placeholder="Enter New Task Description"
      />
      <label>Completed?</label>
      <select name="completed" value={add.completed} onChange={handleChange}>
        <option value="true">✔</option>
        <option value="false">❌</option>
      </select>

      <button
        onClick={() => setShowForm(false)}
        className={`${styles.btn} ${styles.cancel}`}
      >
        Cancel
      </button>
      <button onClick={handleSubmit} className={`${styles.btn} ${styles.add}`}>
        Update Task
      </button>
    </div>
  );
}

export default UpdateTaskForm;
