import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { clear, createTaskAsync } from "../taskSlice";
import Alert from "../../common/Alert";
import styles from "./AddTask.module.css";

const initialState = {
  title: "",
  description: "",
  completed: false,
};

function AddTaskForm({ setShowForm }) {
  const dispatch = useDispatch();
  const [add, setAdd] = useState(initialState);
  const { type, message } = useSelector((state) => state.task);

  function handleChange(e) {
    setAdd({
      ...add,
      [e.target.name]:
        e.target.name === "completed"
          ? e.target.value === "true" // Convert the string to a boolean
          : e.target.value,
    });
  }

  function handleSubmit() {
    dispatch(clear());
    dispatch(createTaskAsync(add));
    setAdd(initialState);
    setShowForm(false);
  }

  return (
    <div className={styles.addTask}>
      {message && <Alert message={message} type={type} />}
      <h2>Add Task</h2>
      <input
        name="title"
        value={add.title}
        onChange={handleChange}
        type="text"
        placeholder="Enter New Task Title"
        required
      />
      <input
        name="description"
        value={add.description}
        onChange={handleChange}
        type="text"
        placeholder="Enter New Task Description"
        required
      />
      <label>Completed?</label>
      <select name="completed" value={add.completed} onChange={handleChange}>
        <option value="true">✔</option>
        <option value="false">❌</option>
      </select>

      <button
        onClick={() => setShowForm(false)}
        className={`${styles.btn} ${styles.cancel}`}
      >
        Cancel
      </button>
      <button onClick={handleSubmit} className={`${styles.btn} ${styles.add}`}>
        Add Task
      </button>
    </div>
  );
}

export default AddTaskForm;
