import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import {
  signUpUser,
  loginUser,
  fetchUserInfo,
  logOut,
  forgotUser,
  resetUser,
  changePass,
  verifyOtp
} from "./authAPI";

const initialState = {
  user: null,
  accessToken: localStorage.getItem("accessToken") || null,
  status: "idle",
  type: null,
  message: null,
};

export const signUpAsync = createAsyncThunk(
  "auth/signUp",
  async (userInfo, { rejectWithValue }) => {
    try {
      const response = await signUpUser(userInfo);
      console.log(response.data);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const verifyOtpAsync = createAsyncThunk(
  "auth/verify",
  async ({email, otp}, { rejectWithValue }) => {
    try {
      const response = await verifyOtp({email, otp});
      console.log(response.data);
      return response.data;
    } catch (message) {
      return rejectWithValue(message.response.data);
    }
  }
);

export const loginAsync = createAsyncThunk(
  "auth/login",
  async (userInfo, { rejectWithValue }) => {
    try {
      const response = await loginUser(userInfo);
      // console.log(response)
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const fetchUserInfoAsync = createAsyncThunk(
  "auth/fetchUser",
  async (accessToken, { rejectWithValue }) => {
    try {
      const response = await fetchUserInfo(accessToken);
      // console.log(response.data)
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const logOutAsync = createAsyncThunk(
  "auth/logout",
  async (accessToken, { rejectWithValue }) => {
    console.log(accessToken);
    try {
      const response = await logOut(accessToken);
      console.log(response.data);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

// coding myself

export const forgotAsync = createAsyncThunk(
  "auth/forgot",
  async (userInfo, { rejectWithValue }) => {
    try {
      const response = await forgotUser(userInfo);
      // console.log(response)
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const resetAsync = createAsyncThunk(
  "auth/reset",
  async ({ password, id, reset_token }, { rejectWithValue }) => {
    try {
      const response = await resetUser(password, id, reset_token);
      // console.log(response)
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const changeAsync = createAsyncThunk(
  "auth/change",
  async ({ password, accessToken }, { rejectWithValue }) => {
    console.log(password, accessToken);
    try {
      const response = await changePass(password, accessToken);
      // console.log(response.data)
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    setAccessToken: (state, action) => {
      state.accessToken = action.payload;
    },
    clear: (state) => {
      state.type = null;
      state.message = null; // Reset message
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(signUpAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(signUpAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.user = null;
        state.accessToken = action.payload.access_token;
        state.type = action.payload.type;
        state.message = action.payload.message;
        localStorage.setItem("accessToken", action.payload.access_token);
      })
      .addCase(signUpAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })

      .addCase(loginAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(loginAsync.fulfilled, (state, action) => {
        // console.log(action.payload)
        state.status = "idle";
        state.user = null;
        state.accessToken = action.payload.access_token;
        state.type = action.payload.type;
        state.message = null;
        localStorage.setItem("accessToken", action.payload.access_token);
      })
      .addCase(loginAsync.rejected, (state, action) => {
        // console.log(action.payload);
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })

      .addCase(fetchUserInfoAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchUserInfoAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
        state.user = action.payload.user;
      })
      .addCase(fetchUserInfoAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })

      .addCase(logOutAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(logOutAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.user = null;
        state.accessToken = null;
        state.type = action.payload.type;
        state.message = action.payload.type;
        localStorage.removeItem("accessToken"); // Remove the token on logout
      })
      .addCase(logOutAsync.rejected, (state, action) => {
        state.status = "idle";
        // console.log(action.payload.message)
        state.type = action.payload.type;
        state.message = action.payload.message;
      })

      // coding myself

      .addCase(forgotAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(forgotAsync.fulfilled, (state, action) => {
        // console.log(action.payload)
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })
      .addCase(forgotAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })

      .addCase(resetAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(resetAsync.fulfilled, (state, action) => {
        // console.log(action.payload)
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })
      .addCase(resetAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })

      .addCase(changeAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(changeAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })
      .addCase(changeAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })

      .addCase(verifyOtpAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(verifyOtpAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.message = action.payload.message;
      })
      .addCase(verifyOtpAsync.rejected, (state, action) => {
        state.status = "idle";
        state.message = action.payload.message;
      });
  },
});

export const { setAccessToken, clear } = authSlice.actions;

export default authSlice.reducer;
