import AddTaskForm from "./components/AddTask";
import UpdateTaskForm from "./components/UpdateTask";

function TaskForm({ update, setUpdate, showForm, setShowForm }) {
  return (
    <div>
      {showForm && (
        <>
          {update ? (
            <UpdateTaskForm
              setShowForm={setShowForm}
              update={update}
              setUpdate={setUpdate}
            />
          ) : (
            <AddTaskForm setShowForm={setShowForm} />
          )}
        </>
      )}
    </div>
  );
}

export default TaskForm;
