import styles from "./Task.module.css"; // Import modular CSS
import Modal from "../../common/Modal";
import { useState } from "react";

function Task({
  id,
  title,
  description,
  completed,
  handleUpdate,
  handleDelete,
}) {
  const [isModalOpen, setModalOpen] = useState(false);

  return (
    <div className={styles.container}>
      {/* <p className={styles.taskId}>Task - {id}</p> */}
      <h2 className={styles.title}>{title}</h2>
      <p className={styles.description}>{description}</p>
      <h4 className={styles.status}>
        Status -{" "}
        <span
          className={completed ? styles.statusCompleted : styles.statusPending}
        >
          {completed ? "Completed" : "Pending"}
        </span>
      </h4>
      <button onClick={() => handleUpdate(id)} className={styles.updateButton}>
        Update
      </button>
      <button
        onClick={() => setModalOpen(true)}
        className={styles.deleteButton}
      >
        Delete
      </button>

      <Modal isOpen={isModalOpen} onClose={() => setModalOpen(false)}>
        <h2>Are you sure you want to delete this task?</h2>
        <button
          onClick={() => setModalOpen(false)}
          className={styles.cancelButton}
        >
          Cancel
        </button>
        <button
          onClick={() => {
            handleDelete(id);
            setModalOpen(false);
          }}
          className={styles.deleteButton}
        >
          Delete
        </button>
      </Modal>
    </div>
  );
}

export default Task;
