import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { fetchAllTasks, createTask, deleteTask, updateTask } from "./taskAPI";

const initialState = {
  tasks: [],
  status: "idle",
  type: null,
  message: null,
};

export const fetchTaskAsync = createAsyncThunk(
  "tasks/fetchTasks",
  async (_, { rejectWithValue }) => {
    try {
      const response = await fetchAllTasks();
      // console.log(response.data);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const createTaskAsync = createAsyncThunk(
  "tasks/createTask",
  async (obj, { rejectWithValue }) => {
    try {
      const response = await createTask(obj);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const deleteTaskAsync = createAsyncThunk(
  "tasks/deleteTask",
  async (id, { rejectWithValue }) => {
    try {
      const response = await deleteTask(id);
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const updateTaskAsync = createAsyncThunk(
  "tasks/updateTask",
  async (obj, { rejectWithValue }) => {
    try {
      const response = await updateTask(obj);
      // console.log(response.data)
      return response.data;
    } catch (error) {
      return rejectWithValue(error.response.data);
    }
  }
);

export const taskSlice = createSlice({
  name: "tasks",
  initialState,
  reducers: {
    clear: (state) => {
      state.type = null;
      state.message = null;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(fetchTaskAsync.pending, (state) => {
        state.status = "loading";
        state.message = null; // Ensure message is null during pending state
      })
      .addCase(fetchTaskAsync.fulfilled, (state, action) => {
        state.status = "idle";
        // console.log(action.payload.tasks)
        state.tasks = action.payload.tasks;
        state.type = action.payload.type;
        state.message = action.payload.message; // Ensure message is null after successful fetch
      })
      .addCase(fetchTaskAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })
      .addCase(createTaskAsync.pending, (state) => {
        state.status = "loading";
        state.message = null; // Ensure message is null during pending state
      })
      .addCase(createTaskAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.tasks.push(action.payload.task);
        state.type = action.payload.type;
        state.message = action.payload.message; // Ensure message is null after successful create
      })
      .addCase(createTaskAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })
      .addCase(deleteTaskAsync.pending, (state) => {
        state.status = "loading";
        state.message = null; // Ensure message is null during pending state
      })
      .addCase(deleteTaskAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.tasks = state.tasks.filter(
          (task) => task.id !== action.payload.id
        );
        state.type = action.payload.type;
        state.message = action.payload.message; // Ensure message is null after successful delete
      })
      .addCase(deleteTaskAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      })
      .addCase(updateTaskAsync.pending, (state) => {
        state.status = "loading";
        state.message = null; // Ensure message is null during pending state
      })
      .addCase(updateTaskAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message; // Ensure message is null after successful update
        const index = state.tasks.findIndex(
          (task) => task.id === action.payload.task.id
        );
        if (index !== -1) {
          state.tasks.splice(index, 1, action.payload.task);
        }
      })
      .addCase(updateTaskAsync.rejected, (state, action) => {
        state.status = "idle";
        state.type = action.payload.type;
        state.message = action.payload.message;
      });
  },
});

export const { clear } = taskSlice.actions;

export default taskSlice.reducer;
