import React, { useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { setAccessToken } from "../features/auth/authSlice";
import { useDispatch } from "react-redux";

function RedirectPage() {
  const { access_token } = useParams(); // Extract access_token from URL
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    if (access_token) {
      // Store the token in localStorage
      localStorage.setItem("accessToken", access_token);

      // Update the Redux state with the token
      dispatch(setAccessToken(access_token));

      // Optionally, redirect to a different page after handling the token
      setTimeout(() => {
        // navigate("/userinfo"); // Or any other page
        navigate("/"); // Or any other page
      }, 0);
    }
  }, [access_token, dispatch, navigate]);

  return (
    <div>
      <h1>Redirecting...</h1>
      <p>
        If you're not redirected automatically,{" "}
        <a href="/userinfo">click here</a>.
      </p>
    </div>
  );
}

export default RedirectPage;
